import React from 'react';
import { View, StyleSheet } from 'react-native';
import { FoilsTable } from './FoilsTable';
import { BaseProduct } from '../types/scraper';
import { SortField } from '../hooks/useSortedFoils';
import { useSortedFoils } from '../hooks/useSortedFoils';

interface ComparisonViewProps {
  foils: BaseProduct[];
  onClose?: () => void;
  onCompare?: () => void;
}

const ComparisonView = ({ foils, onClose, onCompare }: ComparisonViewProps) => {
  const { sortOption, setSortOption, sortedFoils } = useSortedFoils(foils);
  
  const handleSort = (field: SortField) => {
    setSortOption(prev => ({
      field,
      direction: prev.field === field && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  const handleSelect = () => {};
  const isSelected = () => false;

  return (
    <View style={styles.container}>
      <FoilsTable
        foils={sortedFoils}
        onSort={handleSort}
        onFoilSelect={handleSelect}
        isSelected={isSelected}
        sortOption={{
          field: sortOption.field,
          ascending: sortOption.direction === 'asc'
        }}
      />
    </View>
  );
};

export default ComparisonView;

const styles = StyleSheet.create({
  container: {
    flex: 1,
  }
});