import React from 'react';
import { View, Text, Pressable, StyleSheet, Platform } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { spacing } from '../constants/spacing';
import { colors } from '../theme/colors';

interface ComparisonControlsProps {
  selectedFoils: BaseProduct[];
  onCompareSelected: () => void;
  onExitCompare: () => void;
  onClearSelection: () => void;
  isComparisonView?: boolean;
}

const WebWrapper = Platform.select({
  web: ({ children }: { children: React.ReactNode }) => (
    <div style={{
      position: 'fixed',
      bottom: 50,
      left: 0,
      right: 0,
      zIndex: 1000,
      pointerEvents: 'none',
    }}>
      <div style={{ pointerEvents: 'auto', display: 'flex', justifyContent: 'flex-end', paddingRight: spacing.LARGE }}>
        {children}
      </div>
    </div>
  ),
  default: ({ children }: { children: React.ReactNode }) => <>{children}</>,
});

const ComparisonControls = ({ selectedFoils, onCompareSelected, onExitCompare, onClearSelection, isComparisonView }: ComparisonControlsProps) => {
  // Only show controls if there are selected foils or we're in comparison view
  if (selectedFoils.length === 0 && !isComparisonView) {
    return null;
  }

  const content = !isComparisonView ? (
    <View style={styles.comparisonBar}>
      <Text style={styles.selectionText}>
        Selected: {selectedFoils.length}
      </Text>
      <View style={styles.buttonGroup}>
        <Pressable
          style={[styles.button, styles.clearButton]}
          onPress={onClearSelection}
        >
          <Text style={styles.buttonText}>Clear</Text>
        </Pressable>
        <Pressable
          style={[styles.button, selectedFoils.length < 2 && styles.buttonDisabled]}
          onPress={onCompareSelected}
          disabled={selectedFoils.length < 2}
        >
          <Text style={styles.buttonText}>Compare</Text>
        </Pressable>
      </View>
    </View>
  ) : (
    <View style={styles.comparisonBar}>
      <Pressable
        style={[styles.button, styles.cancelButton]}
        onPress={onExitCompare}
      >
        <Text style={styles.buttonText}>Back</Text>
      </Pressable>
    </View>
  );

  return Platform.OS === 'web' ? (
    <WebWrapper>{content}</WebWrapper>
  ) : (
    <View style={styles.container}>
      {content}
    </View>
  );
};

export default ComparisonControls;

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 16,
    backgroundColor: colors.background.secondary,
    borderTopWidth: 1,
    borderTopColor: colors.border,
  },
  comparisonBar: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'flex-end',
    gap: spacing.MEDIUM,
    backgroundColor: 'transparent',
  },
  floatingButton: {
    alignSelf: 'flex-end',
    backgroundColor: '#007AFF',
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    ...Platform.select({
      web: {
        boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
        cursor: 'pointer',
      },
      default: {
        elevation: 2,
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.1,
        shadowRadius: 4,
      }
    })
  },
  buttonGroup: {
    flexDirection: 'row',
    gap: spacing.MEDIUM,
  },
  button: {
    backgroundColor: '#007AFF',
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    ...Platform.select({
      web: {
        boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
        cursor: 'pointer',
      },
      default: {
        elevation: 2,
        shadowColor: '#000',
        shadowOffset: { width: 0, height: 2 },
        shadowOpacity: 0.1,
        shadowRadius: 4,
      }
    })
  },
  buttonDisabled: {
    backgroundColor: '#ccc',
  },
  clearButton: {
    backgroundColor: colors.background.primary,
    borderWidth: 1,
    borderColor: colors.border,
  },
  cancelButton: {
    backgroundColor: '#FF3B30',
  },
  buttonText: {
    color: 'white',
    fontWeight: '600',
    fontSize: 14,
  },
  selectionText: {
    fontSize: 14,
    color: colors.text.secondary,
    marginRight: spacing.MEDIUM,
  },
  selectedCount: {
    color: colors.text.secondary,
    fontSize: 16,
  },
  buttonContainer: {
    flexDirection: 'row',
    gap: 8,
  },
  compareButton: {
    backgroundColor: colors.primary,
  },
  compareButtonText: {
    color: colors.background.primary,
  },
  clearButtonText: {
    color: colors.text.primary,
  }
}); 