/**
 * FoilsTable Component
 * 
 * IMPORTANT: Column Alignment
 * - The table uses fixed column widths for consistent layout and horizontal scrolling
 * - Each column width in headerCell must match the corresponding cell width
 * - Current widths:
 *   - Regular columns (brand, model, type, series): 150px
 *   - Number columns (area, wingspan, AR, price): 100px
 *   - Link column: 100px
 * - When modifying column styles, always update both headerCell and cell styles to maintain alignment
 * - Table has a minWidth of 1200px to ensure proper layout on all screen sizes
 */

import React from 'react';
import { View, Text, Pressable, ScrollView, StyleSheet, Platform, useWindowDimensions } from 'react-native';
import { BaseProduct } from '../types/scraper';
import { SortField } from '../hooks/useSortedFoils';
import { colors } from '../theme/colors';

interface FoilsTableProps {
  foils: BaseProduct[];
  onSort: (field: SortField) => void;
  onFoilSelect: (foil: BaseProduct) => void;
  isSelected: (foil: BaseProduct) => boolean;
  sortOption: { field: SortField; ascending: boolean };
}

const MOBILE_BREAKPOINT = 768;
const TABLE_WIDTH = 850; // 100 + 150 + 80 + 120 + (80 * 4) + 80

export function FoilsTable({
  foils,
  onSort,
  onFoilSelect,
  isSelected,
  sortOption,
}: FoilsTableProps) {
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;

  const renderSortIcon = (field: SortField) => {
    const isActive = sortOption.field === field;
    
    return (
      <View style={styles.sortIconContainer}>
        <Text style={[
          styles.sortIcon,
          styles.sortIconUp,
          isActive && sortOption.ascending && styles.sortIconUpActive
        ]}>‚ñ≤</Text>
        <Text style={[
          styles.sortIcon,
          styles.sortIconDown,
          isActive && !sortOption.ascending && styles.sortIconDownActive
        ]}>‚ñº</Text>
      </View>
    );
  };

  const getHeaderTextStyle = (field: SortField) => {
    const isActive = sortOption.field === field;
    return [
      styles.headerText,
      isActive && sortOption.ascending && styles.headerTextUpActive,
      isActive && !sortOption.ascending && styles.headerTextDownActive,
    ];
  };

  return (
    <View style={styles.container}>
      <View style={styles.tableWrapper}>
        <ScrollView horizontal showsHorizontalScrollIndicator={false} contentContainerStyle={styles.scrollViewContent}>
          <View style={styles.tableContent}>
            <View style={styles.tableHeader}>
              <View style={styles.headerRow}>
                <Pressable style={styles.headerCell} onPress={() => onSort('brand')}>
                  <Text style={getHeaderTextStyle('brand')}>Brand</Text>
                  {renderSortIcon('brand')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.modelCell]} onPress={() => onSort('model')}>
                  <Text style={getHeaderTextStyle('model')}>Model</Text>
                  {renderSortIcon('model')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.typeCell]} onPress={() => onSort('type')}>
                  <Text style={getHeaderTextStyle('type')}>Type</Text>
                  {renderSortIcon('type')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.seriesCell]} onPress={() => onSort('series')}>
                  <Text style={getHeaderTextStyle('series')}>Series</Text>
                  {renderSortIcon('series')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.numberHeaderCell]} onPress={() => onSort('area')}>
                  <Text style={[getHeaderTextStyle('area'), styles.numberHeaderText]}>Area</Text>
                  {renderSortIcon('area')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.numberHeaderCell]} onPress={() => onSort('wingspan')}>
                  <Text style={[getHeaderTextStyle('wingspan'), styles.numberHeaderText]}>Span</Text>
                  {renderSortIcon('wingspan')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.numberHeaderCell]} onPress={() => onSort('aspectRatio')}>
                  <Text style={[getHeaderTextStyle('aspectRatio'), styles.numberHeaderText]}>AR</Text>
                  {renderSortIcon('aspectRatio')}
                </Pressable>
                <Pressable style={[styles.headerCell, styles.numberHeaderCell]} onPress={() => onSort('price')}>
                  <Text style={[getHeaderTextStyle('price'), styles.numberHeaderText]}>Price</Text>
                  {renderSortIcon('price')}
                </Pressable>
                <View style={[styles.headerCell, styles.linkHeaderCell]}>
                  <Text style={[styles.headerText, styles.linkHeaderText]}>Link</Text>
                </View>
              </View>
            </View>
            <View style={styles.scrollViewContainer}>
              <ScrollView style={styles.scrollView}>
                {foils.map((foil, index) => (
                  <Pressable
                    key={`${foil.brand}-${foil.model}`}
                    onPress={() => onFoilSelect(foil)}
                    style={({ pressed }) => [
                      styles.row,
                      index % 2 === 0 && styles.rowEven,
                      isSelected(foil) && styles.rowSelected,
                      Platform.OS === 'web' && pressed && styles.rowHovered
                    ]}
                  >
                    <Text style={styles.cell}>{foil.brand}</Text>
                    <Text style={[styles.cell, styles.modelCell]}>{foil.model}</Text>
                    <Text style={[styles.cell, styles.typeCell]}>{foil.type}</Text>
                    <Text style={[styles.cell, styles.seriesCell]}>{foil.series}</Text>
                    <Text style={[styles.cell, styles.numberCell]}>{foil.specs.area}</Text>
                    <Text style={[styles.cell, styles.numberCell]}>{foil.specs.wingspan}</Text>
                    <Text style={[styles.cell, styles.numberCell]}>{foil.specs.aspectRatio?.toFixed(2)}</Text>
                    <Text style={[styles.cell, styles.numberCell]}>{foil.price ? `$${foil.price}` : '-'}</Text>
                    <View style={[styles.cell, styles.linkCell]}>
                      {foil.sourceUrl && (
                        <Pressable 
                          onPress={(e) => {
                            e.stopPropagation();
                            window.open(foil.sourceUrl, '_blank');
                          }}
                          style={({ pressed }) => [
                            styles.linkButton,
                            pressed && styles.linkButtonPressed
                          ]}
                        >
                          <Text style={styles.linkText}>View</Text>
                        </Pressable>
                      )}
                    </View>
                  </Pressable>
                ))}
              </ScrollView>
            </View>
          </View>
        </ScrollView>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  tableWrapper: {
    width: TABLE_WIDTH,
    backgroundColor: colors.background.primary,
    borderRadius: 8,
    overflow: 'hidden',
    borderWidth: 1,
    borderColor: colors.border,
    flex: 1,
  },
  tableContent: {
    backgroundColor: colors.background.primary,
    width: TABLE_WIDTH,
    flex: 1,
    flexDirection: 'column',
  },
  scrollViewContainer: {
    flex: 1,
  },
  scrollView: {
    flex: 1,
  },
  tableHeader: {
    backgroundColor: colors.background.secondary,
    borderBottomWidth: 1,
    borderBottomColor: colors.border,
    width: TABLE_WIDTH,
  },
  headerRow: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    borderBottomColor: colors.border,
  },
  headerCell: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    padding: 12,
    borderRightWidth: 1,
    borderRightColor: colors.border,
    width: 100,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  headerText: {
    color: colors.text.secondary,
    fontWeight: '600',
    fontSize: 16,
  },
  headerTextUpActive: {
    color: colors.secondary,
    fontWeight: '700',
  },
  headerTextDownActive: {
    color: colors.primary,
    fontWeight: '700',
  },
  row: {
    flexDirection: 'row',
    borderBottomWidth: 1,
    borderBottomColor: colors.border,
    width: TABLE_WIDTH,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  rowEven: {
    backgroundColor: colors.background.secondary,
  },
  rowSelected: {
    backgroundColor: `${colors.primary}15`,
  },
  rowHovered: {
    backgroundColor: `${colors.primary}10`,
  },
  cell: {
    color: colors.text.primary,
    fontSize: 14,
    padding: 12,
    borderRightWidth: 1,
    borderRightColor: colors.border,
    width: 100,
  },
  modelCell: {
    width: 150,
  },
  typeCell: {
    width: 80,
  },
  seriesCell: {
    width: 120,
  },
  numberCell: {
    width: 80,
    textAlign: 'right',
  },
  numberHeaderCell: {
    width: 80,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-between',
    gap: 4,
  },
  numberHeaderText: {
    textAlign: 'left',
    flex: 1,
  },
  linkCell: {
    width: 80,
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 0,
    borderTopRightRadius: Platform.OS === 'web' ? 0 : 8,
    borderBottomRightRadius: Platform.OS === 'web' ? 0 : 8,
  },
  linkHeaderCell: {
    width: 80,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    borderRightWidth: 0,
    borderTopRightRadius: Platform.OS === 'web' ? 0 : 8,
  },
  linkHeaderText: {
    textAlign: 'center',
  },
  linkButton: {
    backgroundColor: colors.primary,
    paddingHorizontal: 8,
    paddingVertical: 4,
    borderRadius: 4,
    minWidth: 44,
    alignItems: 'center',
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  linkButtonPressed: {
    opacity: 0.8,
  },
  linkText: {
    color: colors.background.primary,
    fontSize: 12,
    fontWeight: '600',
  },
  sortIconContainer: {
    flexDirection: 'column',
    alignItems: 'center',
    height: 28,
    justifyContent: 'center',
    width: 16,
    gap: 2,
  },
  sortIcon: {
    color: colors.text.muted,
    fontSize: 12,
    width: 12,
    height: 10,
    lineHeight: 10,
    textAlign: 'center',
  },
  sortIconUp: {
    marginBottom: 0,
  },
  sortIconDown: {
    marginTop: 0,
  },
  sortIconUpActive: {
    color: colors.secondary,
    fontSize: 13,
  },
  sortIconDownActive: {
    color: colors.primary,
    fontSize: 13,
  },
  scrollViewContent: {
    flexGrow: 0,
  },
});
