import React, { useState } from 'react';
import { View, Text, Pressable, StyleSheet, Animated, LayoutChangeEvent } from 'react-native';
import { colors } from '../theme/colors';
import { FilterOptions } from '../types/filters';
import { Ionicons } from '@expo/vector-icons';

interface FilterControlsProps {
  filterOptions: {
    brands: string[];
    series: string[];
    types: string[];
  };
  selectedBrands: string[];
  selectedSeries: string[];
  selectedTypes: string[];
  onFilterChange: (type: keyof FilterOptions, value: string) => void;
}

export function FilterControls({
  filterOptions,
  selectedBrands,
  selectedSeries,
  selectedTypes,
  onFilterChange,
}: FilterControlsProps) {
  const [isExpanded, setIsExpanded] = useState(false);
  const [animation] = useState(new Animated.Value(0));
  const [contentHeight, setContentHeight] = useState(0);

  const toggleExpanded = () => {
    const toValue = isExpanded ? 0 : 1;
    Animated.spring(animation, {
      toValue,
      useNativeDriver: false,
      friction: 10,
    }).start();
    setIsExpanded(!isExpanded);
  };

  const handleLayout = (event: LayoutChangeEvent) => {
    const { height } = event.nativeEvent.layout;
    setContentHeight(height);
  };

  const animatedHeight = animation.interpolate({
    inputRange: [0, 1],
    outputRange: [0, contentHeight],
  });

  return (
    <View style={styles.wrapper}>
      <Pressable onPress={toggleExpanded} style={styles.header}>
        <View style={styles.headerContent}>
          <Text style={styles.headerTitle}>Filters</Text>
          <View style={styles.filterSummary}>
            {selectedBrands.length > 0 && (
              <Text style={styles.filterCount}>Brands: {selectedBrands.length}</Text>
            )}
            {selectedTypes.length > 0 && (
              <Text style={styles.filterCount}>Types: {selectedTypes.length}</Text>
            )}
            {selectedSeries.length > 0 && (
              <Text style={styles.filterCount}>Series: {selectedSeries.length}</Text>
            )}
          </View>
        </View>
        <Ionicons 
          name={isExpanded ? 'chevron-up' : 'chevron-down'} 
          size={24} 
          color={colors.text.secondary}
        />
      </Pressable>

      <Animated.View style={[styles.content, { height: animatedHeight }]}>
        <View style={styles.container} onLayout={handleLayout}>
          <View style={styles.section}>
            <Text style={styles.sectionTitle}>Brand</Text>
            <View style={styles.chipContainer}>
              {filterOptions.brands.map((brand) => (
                <Pressable
                  key={brand}
                  style={[
                    styles.chip,
                    selectedBrands.includes(brand) && styles.chipSelected,
                  ]}
                  onPress={() => onFilterChange('brands', brand)}
                >
                  <Text 
                    style={[
                      styles.chipText,
                      selectedBrands.includes(brand) && styles.chipTextSelected,
                    ]}
                  >
                    {brand}
                  </Text>
                </Pressable>
              ))}
            </View>
          </View>

          <View style={styles.section}>
            <Text style={styles.sectionTitle}>Type</Text>
            <View style={styles.chipContainer}>
              {filterOptions.types.map((type) => (
                <Pressable
                  key={type}
                  style={[
                    styles.chip,
                    selectedTypes.includes(type) && styles.chipSelected,
                  ]}
                  onPress={() => onFilterChange('type', type)}
                >
                  <Text 
                    style={[
                      styles.chipText,
                      selectedTypes.includes(type) && styles.chipTextSelected,
                    ]}
                  >
                    {type}
                  </Text>
                </Pressable>
              ))}
            </View>
          </View>

          <View style={styles.section}>
            <Text style={styles.sectionTitle}>Series</Text>
            <View style={styles.chipContainer}>
              {filterOptions.series.map((series) => (
                <Pressable
                  key={series}
                  style={[
                    styles.chip,
                    selectedSeries.includes(series) && styles.chipSelected,
                  ]}
                  onPress={() => onFilterChange('series', series)}
                >
                  <Text 
                    style={[
                      styles.chipText,
                      selectedSeries.includes(series) && styles.chipTextSelected,
                    ]}
                  >
                    {series}
                  </Text>
                </Pressable>
              ))}
            </View>
          </View>
        </View>
      </Animated.View>
    </View>
  );
}

const styles = StyleSheet.create({
  wrapper: {
    backgroundColor: colors.background.primary,
    marginBottom: 16,
    borderRadius: 8,
    borderWidth: 1,
    borderColor: colors.border,
    overflow: 'hidden',
  },
  header: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: 16,
    backgroundColor: colors.background.primary,
  },
  headerContent: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'center',
    gap: 16,
  },
  headerTitle: {
    fontSize: 16,
    fontWeight: '600',
    color: colors.text.primary,
  },
  filterSummary: {
    flexDirection: 'row',
    gap: 12,
  },
  filterCount: {
    fontSize: 14,
    color: colors.text.secondary,
  },
  content: {
    overflow: 'hidden',
  },
  container: {
    padding: 16,
    paddingTop: 0,
  },
  section: {
    marginBottom: 16,
  },
  sectionTitle: {
    fontSize: 14,
    fontWeight: '600',
    color: colors.text.primary,
    marginBottom: 8,
  },
  chipContainer: {
    flexDirection: 'row',
    flexWrap: 'wrap',
    gap: 8,
  },
  chip: {
    backgroundColor: colors.background.secondary,
    borderRadius: 16,
    paddingHorizontal: 12,
    paddingVertical: 6,
  },
  chipSelected: {
    backgroundColor: colors.primary,
  },
  chipText: {
    color: colors.text.secondary,
    fontSize: 14,
  },
  chipTextSelected: {
    color: colors.background.primary,
  },
}); 